.card-icon {
  grid-column: span 3;

  @include rwd-df-down(md) {
    grid-column: span 6;
  }

  @include rwd-df-down(xs) {
    grid-column: span 12;
  }

  border: 1px solid #e5e5e5;
  border-radius: 2rem;
  background-color: $color-white;
  padding: 1rem;

  transition: all 0.3s ease-in;

  &:hover {
    transform: scale(1.02);
    box-shadow: 0 0 12px 0 rgba($color-black, 0.12);
  }

  &__header {
    display: flex;
    // justify-content: center;
    align-items: center;
    @extend .u-mb-3;

    @include rwd-df-down(xs) {
      justify-content: center;
    }
  }

  &__icon-container {
    width: 4.5rem;
    height: 4.5rem;
    border-radius: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0.2rem solid $color-primary;
    margin-#{$end}: 1rem;
    &__icon {
      fill: $color-primary;
      width: 3rem;
      height: 3rem;
    }
  }

  &__title {
    font-family: $text-font;
    font-size: 1.8rem;
    font-weight: 500;
    color: $color-black;
    text-transform: capitalize;
  }

  &__text {
    font-family: $text-font;
    font-size: 1.3rem;
    font-weight: normal;
    line-height: 1.85;
    color: $color-third-dark-1;
  }

  // &:hover &__icon-container {
  //   background-color: $color-fourth;
  //   .card-icon__icon-container__icon {
  //     fill: $color-primary-light-1;
  //   }
  // }
}
