.product-card {
  grid-column: span 3;

  @include rwd-df-down(md) {
    grid-column: span 6;
  }

  @include rwd-df-down(xs) {
    grid-column: span 12;
  }

  transition: all 0.3s ease-in;
  &:hover {
    box-shadow: 0 0 12px 0 rgb(0 0 0 / 12%);
  }

  border: 1px solid #e5e5e5;
  background-color: $color-white;
  padding: 0.75rem;

  &__header {
    position: relative;
    height: 30rem;

    &:hover .product-card__overlay {
      opacity: 1;
      z-index: 2;
    }
  }

  &__notify {
    position: absolute;
    z-index: 3;
    color: $color-white;
    font-family: $text-font;
    font-size: 1.2rem;
    font-weight: 700;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 4rem;
    height: 4rem;
    background-color: $color-primary;
    border-radius: 50%;

    &--discount {
      top: 1rem;
      left: 1rem;
      border-radius: 2rem;
      width: 5rem;
      height: 3rem;
    }

    &--status {
      bottom: 1rem;
      left: 1rem;
    }

    &--whishlist {
      top: 1rem;
      right: 1rem;

      &__icon {
        width: 2rem;
        fill: $color-white;
      }
    }
  }

  &__image {
    display: block;
    height: 100%;
    width: 100%;
    border: 1px solid $color-third-dark-3;
  }

  &__overlay {
    opacity: 0;
    z-index: -1;
    transition: all 0.3s ease-in;
    cursor: pointer;

    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba($color-black, 0.3);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: $color-white;
    border: 0.7rem solid #ccc;

    &__icon-container {
      text-align: center;
      &__icon {
        width: 8rem;
        height: 8rem;
        fill: $color-white;
      }
    }
    &__text {
      text-align: center;
      text-transform: uppercase;
      font-size: $default-font-size;
      font-family: $header-font;
      font-weight: 900;
      letter-spacing: 3px;
    }
  }

  &__discount {
  }

  &__status {
  }

  &__wishlist {
  }

  &__body {
    margin: 1.5rem 0;
    text-align: center;
  }

  &__title {
    font-family: $header-font;
    font-weight: 700;
    font-size: $default-font-size;
    color: $color-black-light-1;
  }

  &__price {
    display: inline-block;
    font-family: $text-font;
    font-weight: 700;
    font-size: $default-font-size;
    color: $color-primary;

    &--old {
      margin-#{$end}: 1.5rem;
      color: $color-third-dark-1;
      text-decoration: line-through;
    }
    &--new {
    }
  }
}
