.scroll-to-top-btn {
  position: fixed;
  bottom: 4.5rem;
  right: 1.5rem;
  z-index: 100;
  padding: 0.5rem;
  background-color: #e69d00;
  color: #fff7e6;
  width: 4rem;
  height: 4rem;
  font-size: 1.6rem;
  border-radius: 0.8rem;
  font-weight: 700;
  line-height: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;

  display: block;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transform: scale(0);
  transition: all 0.8s;
  border: 0.2rem solid transparent;

  &.show {
    visibility: visible;
    opacity: 1;
    transform: scale(1);
    pointer-events: auto;

    &:hover {
      background-color: #fff7e6;
      color: #e69d00;
      border-color: #e69d00;
      border-radius: 0;
    }
  }
}
