.country-container {
  display: flex;
  align-items: center;
}

.flag {
  background-size: 30px 20px;
  height: 20px;
  width: 30px;
  display: inline-block;
  margin: 0 2px 0 0;
  flex: 0 0 30px;
}

.countries {
  // display: inline-block;
  font-family: inherit;
  // background-color: #eee;
  // background-color: $color-third-light-1;
  background-color: $color-white;
  padding: 0.2rem;
  width: 10rem;
  border-radius: 0.2rem;
  border: none;
  outline: none;
  cursor: pointer;
}

.currency-container {
  margin-left: 1rem;
  // border-left: 0.1rem solid black;
  padding-left: 1rem;
  .currency-selector {
    padding: 0.2rem;
    height: 100%;
    font-family: inherit;
    // background-color: #eee;
    // background-color: $color-third-light-1;
    background-color: $color-white;
    border: none;
    outline: none;
    cursor: pointer;
  }
}

.lang-container {
  margin-left: 1rem;
  border-left: 0.1rem solid black;
  padding-left: 1rem;

  .lang-selector {
    padding: 0.2rem;
    height: 100%;
    font-family: inherit;
    // background-color: #eee;
    // background-color: $color-third-light-1;
    background-color: $color-white;
    border: none;
    outline: none;
    cursor: pointer;
  }
}
