.category-card {
  display: block;
  border: 1px solid #e5e5e5;
  background-color: $color-white;
  border-radius: 0.5rem;
  grid-column: span 2;
  cursor: pointer;
  transition: all 0.3s ease-in;
  text-decoration: none;
  color: inherit;

  &:hover {
    transform: scale(1.02);
    box-shadow: 0 0 12px 0 rgba($color-black, 0.12);
  }

  grid-column: span 2;

  @include rwd-df-down(lg) {
    grid-column: span 3;
  }

  @include rwd-df-down(md) {
    grid-column: span 4;
  }

  @include rwd-df-down(sm) {
    grid-column: span 6;
  }

  @include rwd-only(xxs) {
    grid-column: span 12;
  }

  &__image {
    display: block;
    height: 18rem;
    width: 100%;
    border-radius: 0.5rem;
    background-color: #f5f5f5;
  }

  &__text {
    font-family: $text-font;
    font-size: $default-font-size;
    font-weight: normal;
    text-transform: capitalize;
    letter-spacing: 0.25rem;
    text-align: center;
    padding: 1rem;
  }
}
