.adv-nav {
  @include rwd-df-down(md) {
    display: none;
  }

  font-size: 1.2rem;
  // background-color: #f8fafb;
  // background-color: #eee;
  // background-color: $color-third-light-1;
  background-color: $color-white;
  // background-image: linear-gradient(
  //   to bottom,
  //   $color-primary,
  //   rgba($color-primary, 0.7)
  // );
  padding: 0.3rem 0;
  &__content {
    display: flex;
    gap: 2rem;
    justify-content: space-between;
    align-items: stretch;
  }

  &__item {
    display: flex;
    align-items: center;
  }

  .sign {
    padding: 1rem 0;
    font-size: inherit;
    position: relative;
    text-transform: capitalize;
    // background-color: red;

    display: flex;
    align-items: center;

    &__text {
    }

    &__title {
      text-decoration: underline;
    }

    &__tooltip {
      display: none;
      font-size: 1.2rem;
      position: absolute;
      z-index: 103;
      top: calc(100%);
      right: 0;
      width: 28rem;
      background-color: $color-white;
      border: 0.1rem solid black;
      padding: 3rem;
      text-align: center;

      &__title {
        text-align: center;
        font-weight: 500;
        font-size: inherit;
        text-transform: capitalize;
        @extend .u-mb-4;
      }

      &__register {
        display: inline-block;
        width: 100%;
        padding: 1rem;
        cursor: pointer;
        text-decoration: none;
        text-transform: uppercase;
        font-weight: bold;
        font-size: inherit;
        @extend .u-mb-3;
        // margin-bottom: 1rem;
        border: 0.1rem solid $color-black;
        transition: all 0.5s;

        &:hover {
          opacity: 0.7;
        }

        &--signin {
          font-size: inherit;
          background-color: $color-black;
          color: $color-white;
        }

        &--signup {
          font-size: inherit;
          background-color: $color-white;
          color: $color-black;
          &:hover {
            background-color: $color-black;
            color: $color-white;
          }
        }
      }

      &__check {
        transition: all 0.5s;
        font-size: inherit;
        color: $color-black;
        text-transform: capitalize;
        font-weight: normal;
        &:hover {
          text-decoration: none;
          opacity: 0.8;
        }
      }

      &::before {
        content: '';
        position: absolute;
        padding: 0.5rem;
        top: -0.65rem;
        right: 1rem;
        background-color: $color-white;
        transform: rotate(45deg);
        display: inline-block;
        border-width: 0.1rem;
        border-style: solid;
        border-top-color: black;
        border-right-color: transparent;
        border-bottom-color: transparent;
        border-left-color: black;
      }
    }
  }

  .sign:hover .sign__tooltip {
    display: block;
  }

  .slider-adv {
    flex: 1 0 auto;
  }
}
