.slider-adv {
  // background-color: #eee;

  &:hover {
    // background-color: green;
    cursor: grabbing;
    cursor: -webkit-grabbing;
  }

  &__container {
    // background-color: lightblue;
    height: 4rem;
    position: relative;
    overflow: hidden;
  }

  &__item {
    // background-color: rgba(lightseagreen, 0.5);
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    height: 100%;

    transform: translateX(0);
    transition: transform 0.5s ease-in-out;

    display: flex;
    justify-content: center;
    align-items: center;
    // border: 1px solid $color-black;
    &__text {
      font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
      font-size: 1.2rem;
      font-style: italic;
      font-weight: bolder;
      color: $color-black;
    }
  }

  &__nav {
    &__btn {
      display: inline-block;
      padding: 1rem;
      background-color: transparent;
      border: none;
      cursor: pointer;
      font-weight: 700;
      font-size: 1.6rem;

      &-prev {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
      }

      &-next {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 0;
      }
    }
  }
}
