.header {
  position: sticky;
  top: 0;
  left: 0;
  z-index: 101;
}

/*start  define styling for main-nav component */
.main-nav {
  opacity: 0.95;
  font-family: $header-font;

  background-color: $color-white;
  // border-bottom: 2px solid $color-primary;
  box-shadow: 0 0.1rem 0.2rem rgba($color-black, 0.4);

  .navigation {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 1rem 4rem;

    @include rwd-df-down(md) {
      padding: 1rem 3rem;
    }

    @include rwd-df-down(xs) {
      padding: 1rem 2rem;
    }

    /* logo image*/
    &__logo-image {
      display: block;
      height: 5rem;
      width: auto;
      cursor: pointer;
    }

    /* ul list*/
    &__list {
      margin-left: 5rem;
      display: flex;
      justify-content: space-between;
      gap: 3rem;

      transition: max-height 0.5s ease-in-out;

      @include rwd-df-down(lg) {
        margin-left: 2rem;
        gap: 1.6rem;
      }

      @include rwd-df-down(md) {
        display: flex;
        flex-direction: column;
        gap: 0;
        width: 100%;
        margin: 0;

        // display: none;
        max-height: 0;
        overflow: hidden;

        & li:first-child {
          margin-top: 1rem;
        }

        &.show {
          // height: auto;
          // margin: 1rem 0 0 0;
          max-height: 100vh;
        }
      }
    }

    /*ul li item*/
    &__item {
      @include rwd-df-down(md) {
        width: 100%;
        &:not(:last-child) {
          border-bottom: 0.1rem solid $color-fourth;
        }
      }
    }

    /*ul li a*/
    &__link {
      font-family: $header-font;
      font-size: 1.8rem;
      font-weight: 700;
      text-decoration: none;
      text-transform: uppercase;

      @include rwd-df-down(lg) {
        font-size: 1.6rem;
      }

      &:link,
      &:visited {
        color: $color-third-dark-1;
      }

      &:hover,
      &:active,
      &--active:link,
      &--active:visited {
        color: rgba($color-primary, 0.9);
      }

      @include rwd-df-down(md) {
        font-size: 1.6rem;
        font-weight: 700;
        display: block;
        width: 100%;
        padding: 0.5rem 0;
        text-align: center;
        transition: all 0.3s;

        &:hover,
        &:active,
        &--active:link,
        &--active:visited {
          background-color: rgba($color-primary, 0.9);
          color: #fff;
        }
      }

      @include rwd-df-down(xs) {
        font-size: 1.4rem;
      }
    }

    &__toggle-btn {
      border: none;
      width: 4.3rem;
      height: 4.3rem;
      border-radius: 0.4rem;
      background-color: $color-primary;
      padding: 1rem 0.7rem;
      cursor: pointer;

      display: flex;
      flex-direction: column;
      justify-content: space-between;
      transition: box-shadow 0.3s, transform 0.3s;

      &:hover {
        justify-content: space-around;
        transform: translateY(-0.1rem);
        box-shadow: 0 0.5rem 0.2rem rgba($color-black, 0.4);
      }

      &:focus {
        outline: none;
        transform: translateY(0);
        box-shadow: 0 0.5rem 0.3rem rgba($color-black, 0.4);
      }

      & span {
        display: block;
        width: 100%;
        height: 0.35rem;
        background-color: $color-white;
        border-radius: 2rem;
      }

      display: none;
      @include rwd-df-down(md) {
        display: flex;
      }
    }
  }
}
/*end*/
