/*start  define styling for footer component */
.footer {
  background-color: $color-black-light-2;
  color: $color-white;

  .primary-footer {
    &__main-content {
      padding: 1rem;
      @extend .u-grid;
      gap: 2rem;
      @include rwd-df-down(xs) {
        gap: 3rem 0;
      }

      &__item {
        grid-column: span 4;

        &--info {
          grid-column: span 6;

          @include rwd-df-down(md) {
            grid-column: span 12;
          }
        }

        &--links {
          grid-column: span 3;

          @include rwd-df-down(md) {
            grid-column: span 6;
          }

          @include rwd-df-down(xs) {
            grid-column: span 12;
          }
        }

        &__title {
          color: $color-white;
          text-decoration: underline;
          text-transform: uppercase;
          font-size: 1.8rem;
          font-weight: 600;
          line-height: 1.4rem;
          @extend .u-mb-5;
          @include rwd-df-down(xs) {
            text-align: center;
          }
        }

        &__list {
          &__item {
            &:not(:last-child) {
              @extend .u-mb-3;
            }

            &__link {
              &:link,
              &:visited {
                color: $color-white;
                text-decoration: none;
                text-transform: capitalize;
              }

              &:hover,
              &:active {
                color: $color-primary;
                text-decoration: underline;
              }
            }
          }
        }
      }
    }

    &__divider {
      height: 1px;
      background-color: $color-third-dark-1;
      @extend .u-my-5;
    }

    &__sub-content {
      padding: 1rem;
      @extend .u-grid;
      gap: 2rem;
      @include rwd-df-down(xs) {
        gap: 3rem 0;
      }

      &__item {
        grid-column: span 6;

        @include rwd-df-down(xs) {
          grid-column: span 12;
        }

        &__title {
          color: $color-white;
          text-transform: uppercase;
          font-size: 1.8rem;
          font-weight: 600;
          line-height: 1.4rem;
          @extend .u-mb-5;

          @include rwd-df-down(xs) {
            text-align: center;
          }
        }

        &__list {
          display: flex;
          @include rwd-df-down(xs) {
            justify-content: center;
          }
          &__item {
            &:not(:last-child) {
              margin-right: 1rem;
            }

            .icon-container {
              display: block;
              width: 3.5rem;
              height: 3.5rem;
              border-radius: 0.5rem;
              display: flex;
              justify-content: center;
              align-items: center;
              border: 0.2rem solid $color-third-light-1;
              margin-#{$end}: 1rem;
              &__icon {
                fill: $color-third-light-1;
                width: 2.6rem;
                height: 2.6rem;
              }

              &:hover {
                border: 0.2rem solid $color-primary;
                .icon-container__icon {
                  fill: $color-primary;
                }
              }
            }
          }
        }
      }
    }
  }

  .icon-card {
    display: flex;
    align-items: center;

    &__icon-container {
      width: 3.5rem;
      height: 3.5rem;
      border-radius: 0.5rem;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 0.2rem solid $color-primary;
      margin-#{$end}: 1rem;
      &__icon {
        fill: $color-primary;
        width: 2.5rem;
        height: 2.5rem;
      }
    }

    &__info {
      &__title {
        font-family: $text-font;
        font-size: 1.8rem;
        font-weight: 500;
        color: $color-white;
        text-transform: capitalize;
      }

      &__text {
        font-family: $text-font;
        font-size: 1.3rem;
        font-weight: normal;
        line-height: 1.85;
        color: $color-third-light-1;
      }
    }
  }

