.offer-card {
  display: block;
  border: 1px solid #e5e5e5;
  background-color: $color-white;
  border-radius: 0.5rem;
  grid-column: span 2;
  cursor: pointer;
  transition: all 0.3s ease-in;

  &:hover {
    transform: scale(1.02);
    box-shadow: 0 0 12px 0 rgba($color-black, 0.12);
  }

  grid-column: span 4;

  @include rwd-df-down(lg) {
    grid-column: span 6;
  }

  // @include rwd-df-down(md) {
  //   grid-column: span 4;
  // }

  @include rwd-df-down(sm) {
    grid-column: span 12;
  }

  &__image {
    display: block;
    height: 25rem;
    width: 100%;
    border-radius: 0.5rem;
    background-color: #f5f5f5;
  }
}
