/* define styling for toast component */

.toast {
  opacity: 0;
  z-index: -1;
  transition: all 1s ease-in-out;
  position: fixed;
  top: 68px;
  left: 10px;
  max-width: 7000px;
  font-size: 0.875rem;
  // background-color: #242526;
  // background-color: green;
  border-radius: 16px;

  &__container {
    font-size: 1.4rem;
    color: #d9dce1;
    background-color: #242526;
    border-bottom: 0px;
    border-radius: 16px;
    padding: 2rem 6rem 2rem 2rem;
  }

  &__text {
  }

  &__icon {
  }

  &__close-btn {
    display: block;
    padding: 0;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 2rem;
    border: none;
    width: 3rem;
    height: 3rem;
    background-color: #3a3b3c;
    color: $color-white;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: all 0.3s;
    outline: none;

    &__icon {
      display: block;
      padding: 0;
      margin: 0;
      width: 100%;
      height: 100%;
      font-style: normal;
      font-size: 2.3rem;
      line-height: 1.35;
      color: inherit;
    }

    &:hover {
      background-color: $color-white;
      color: #3a3b3c;
    }
  }
}
