/* start  define styling website sections */

// #SECTION:start
.sec-newsletter {
  font-family: inherit;
  background-color: $color-black-light-1;
  color: $color-white;

  &__content {
    display: grid;
    grid-template-columns: repeat(12, 1fr);

    gap: 1rem;
    @include rwd-df-down(md) {
      gap: 1.5rem;
    }

    @include rwd-df-down(sm) {
      gap: 3rem 0;
    }
  }

  .sec-newsletter__item--logo {
    grid-column: span 2;
    display: block;
    @include rwd-df-down(md) {
      display: none;
    }

    &__logo {
      display: block;
      padding: 0 1rem;
      height: 9rem;
      filter: invert(99%) sepia(100%) saturate(5%) hue-rotate(253deg)
        brightness(108%) contrast(100%);
      // filter: invert(67%) sepia(24%) saturate(6799%) hue-rotate(349deg) brightness(104%) contrast(93%);
    }
  }

  &__item--form {
    grid-column: span 6;

    @include rwd-df-down(md) {
      grid-column: span 7;
    }

    @include rwd-df-down(sm) {
      grid-column: span 12;
      text-align: center;
    }

    font-family: inherit;
    .newsletter {
      &__title {
        font-size: 1.8rem;
        font-weight: 700;
        text-transform: uppercase;
        color: $color-white;
        // @extend .u-mb-3;
      }

      &__text {
        font-family: $text-font;
        color: $color-third-1;
        font-size: 1.3rem;
        font-weight: 400;
        line-height: 1.85;
        @extend .u-mb-3;
      }

      &__form {
        display: flex;
        justify-content: flex-start;

        @include rwd-df-down(sm) {
          justify-content: center;
          margin: 0 auto;
        }

        @include rwd-df-down(xs) {
          flex-direction: column;
          align-items: center;
        }

        &__email {
          position: relative;
          flex: 1 1 auto;
          @include rwd-df-down(xs) {
            width: 70%;
          }

          &__input {
            display: block;
            background-color: $color-white;
            border: 0.2rem solid $color-white;
            padding: 0.8rem;
            padding-#{$start}: 3.4rem;
            border-radius: 0.4rem;
            outline: none;
            font-family: inherit;
            font-size: 1.2rem;
            transition: all 0.3s ease-in;
            width: 100%;
            &:hover {
              border: 0.2rem solid $color-primary;
            }
          }
          &__icon {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            width: 2rem;
            left: 0.8rem;
            fill: $color-third-1;
          }
        }
        &__submit-btn {
          display: block;
          padding: 0.8rem 1.6rem;
          text-transform: uppercase;
          font-size: 1.2rem;
          font-family: inherit;
          font-weight: bold;
          border: 0.1rem solid $color-white;
          background-color: $color-black-light-1;
          color: $color-white;
          border-radius: 0.4rem;
          margin-#{$start}: 1.5rem;
          transition: all 0.3s ease-in;
          cursor: pointer;

          @include rwd-df-down(xs) {
            width: 50%;
            margin-#{$start}: 0rem;
            margin-top: 1rem;
          }

          &:hover {
            color: $color-primary;
            border: 0.1rem solid $color-primary;
          }
        }
      }
    }
  }

  &__item--apps {
    grid-column: span 4;

    @include rwd-df-down(md) {
      grid-column: span 5;
    }
    @include rwd-df-down(sm) {
      grid-column: span 12;
    }

    .apps-content__text {
      display: flex;
      align-items: flex-start;
      @extend .u-mb-3;

      @include rwd-df-down(sm) {
        justify-content: center;
      }
    }
    .apps-content__icon-container {
      padding: 0.3rem;
      background-color: $color-primary;
      border-radius: 0.4rem;
      &__logo {
        width: 3.5rem;
        // height: 3rem;
        filter: invert(99%) sepia(100%) saturate(5%) hue-rotate(253deg)
          brightness(108%) contrast(100%);
      }
    }
    .apps-content__text__info {
      margin-#{$start}: 1rem;
      &__title {
        font-size: 1.8rem;
        font-weight: 700;
        // @extend .u-mb-1;
      }

      &__des {
        font-family: $text-font;
        font-size: 1.2rem;
        line-height: 1.85;
        font-weight: 400;
        color: $color-third-1;
      }
    }

    .apps-content__logos {
      display: flex;
      justify-content: flex-start;

      @include rwd-df-down(sm) {
        justify-content: center;
      }

      .apps-content__logo-link {
        display: inline-block;
        width: 10rem;
        height: 4rem;
        text-decoration: none;
        border: 0.1rem solid $color-white;
        border-radius: 0.4rem;
        transition: all 0.3s ease-in;

        &:not(:first-child) {
          margin-#{$start}: 1rem;
        }
      }

      .apps-content__logo {
        width: 100%;
        height: 100%;
        display: inline-block;
        padding: 0.5rem;
        fill: $color-white;
        transition: all 0.3s ease-in;
      }

      //   .apps-content__logo-link {
      //     display: block;
      //     text-decoration: none;
      //     border: 0.1rem solid $color-white;
      //     border-radius: 0.4rem;
      //     transition: all 0.3s ease-in;
      //   }

      //   .apps-content__logo {
      //     display: block;
      //     padding: 0.5rem;
      //     width: 100%;
      //     height: 3.5rem;
      //     fill: $color-white;
      //     transition: all 0.3s ease-in;
      //   }

      .apps-content__logo-link:hover {
        border: 0.1rem solid $color-primary;
        .apps-content__logo {
          fill: $color-primary;
        }
      }
    }
  }
}
// #SECTION:end

.sec-content {
  border-radius: 0.5rem;
  box-shadow: 0 2px 5px 0 rgba($color-black, 0.5);
  padding: 1rem;
}

