.main-slider {
  background-color: #eee;
  position: relative;

  margin-bottom: 10rem;
  @include rwd-df-down(md) {
    margin-bottom: 0;
  }

  &:hover {
    // background-color: green;
    cursor: grabbing;
    cursor: -webkit-grabbing;
  }

  &__container {
    background-color: lightblue;
    height: 75vh;
    position: relative;
    overflow: hidden;
  }

  &__item {
    // background-color: rgba(lightseagreen, 0.5);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center top;
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    height: 100%;

    transform: translateX(0);
    transition: transform 0.5s ease-in-out;

    // display: flex;
    // justify-content: center;
    // align-items: center;
    // border: 2px solid red;

    &--1 {
      background-image: linear-gradient(
          to top,
          rgba($color-black, 0.3),
          rgba($color-black, 0.4)
        ),
        url('/content/img/main-slider/1.jpg');
    }

    &--2 {
      background-image: linear-gradient(
          to top,
          rgba($color-black, 0.3),
          rgba($color-black, 0.4)
        ),
        url('/content/img/main-slider/2.jpg');
    }

    &--3 {
      background-image: linear-gradient(
          to top,
          rgba($color-black, 0.3),
          rgba($color-black, 0.4)
        ),
        url('/content/img/main-slider/3.jpg');
    }

    &--4 {
      background-image: linear-gradient(
          to top,
          rgba($color-black, 0.3),
          rgba($color-black, 0.4)
        ),
        url('/content/img/main-slider/4.jpg');
    }

    &__text {
      font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
      font-size: 5rem;
      font-style: italic;
      font-weight: bolder;
      color: white;
    }
  }

  &__text {
    // margin-#{$start}: 20rem;
    position: absolute;
    top: 25%;
    left: 6%;

    @include rwd-df-down(xs) {
      top: 55%;
      left: 6%;
    }

    &__title {
      font-family: $header-font;
      font-size: 5rem;
      @include rwd-df-down(md) {
        font-size: 4rem;
      }
      @include rwd-df-down(sm) {
        font-size: 3rem;
      }

      @include rwd-df-down(xs) {
        font-size: 4rem;
      }
      font-weight: 700;
      line-height: 1.3;
      color: $color-white;
      text-transform: capitalize;
    }

    .shop-link {
      @extend .u-mt-5;
      text-decoration: none;
      display: inline-flex;
      align-items: center;
      &:hover {
        opacity: 0.7;
      }
      &__icon-container {
        margin-#{$end}: 2rem;
        border-radius: 50%;
        background-color: $color-primary;
        width: 4rem;
        height: 4rem;
        display: flex;
        justify-content: center;
        align-items: center;
        &__icon {
          width: 1.6rem;
          height: 2rem;
          fill: $color-white;
        }
      }
      &__text {
        color: white;
        font-family: $text-font;
        font-size: $default-font-size;
        text-transform: uppercase;
      }
    }
  }

  &__nav {
    position: absolute;
    right: 3rem;
    bottom: 3rem;
    display: flex;
    flex-direction: column;

    @include rwd-df-down(md) {
      bottom: 6rem;
      right: 2rem;
    }

    @include rwd-df-down(xs) {
      display: none;
    }

    &__btn {
      display: block;
      width: 5rem;
      height: 5rem;

      &:not(:last-child) {
        margin-bottom: 0.5rem;
      }
      // padding: 1rem 2rem;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: $color-white;
      border: none;
      cursor: pointer;

      transition: all 0.3s ease-in;

      &__icon {
        width: 40%;
        transition: all 0.3s ease-in;
      }

      &:hover {
        background-color: rgba($color-black, 0.4);
      }

      &:hover &__icon {
        fill: white;
      }

      &-prev {
      }
      &-prev &__icon {
      }

      &-next {
      }
      &-next &__icon {
      }
    }
  }

  &__indicators {
    position: absolute;
    z-index: 2;
    right: 3.5rem;
    top: 40%;
    transform: translateY(-50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @include rwd-df-down(md) {
      top: 90%;
      right: 50%;
      transform: translateX(50%);
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
    }

    @include rwd-df-down(xs) {
      display: none;
    }

    &__number {
      // writing-mode: vertical-lr;
      // transform: rotate(180deg);
      font-family: inherit;
      margin: 0 1rem;
      color: $color-white;

      &--currentNumber {
      }

      &--totalNumber {
      }
    }

    &__item {
      display: block;
      height: 8rem;
      width: 0.3rem;
      @include rwd-df-down(md) {
        width: 8rem;
        height: 0.3rem;
      }

      padding: 0;
      border: none;
      border-radius: 2rem;
      // background-color: lightslategrey;
      background-color: rgba($color-black, 0.4);
      // margin: 0 1rem;
      cursor: pointer;

      &:hover,
      &--active {
        background-color: $color-white;
      }
    }
  }

  &__features {
    position: absolute;
    z-index: 2;
    top: 100%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: 80%;
    box-shadow: 0 2px 3px 0 rgba($color-black, 0.4);
    padding: 2.5rem;
    border-radius: 1rem;
    @extend .u-grid;
    background-color: $color-white;

    @include rwd-df-down(md) {
      display: none;
    }

    .feature {
      @extend .u-col-4;
      display: flex;
      justify-content: center;

      &__icon-container {
        margin-#{$end}: 1rem;
        width: 5rem;
        height: 5rem;
        display: flex;
        border-radius: 1rem;
        justify-content: center;
        align-items: center;
        // background-color: $color-fourth;
        // &__icon {
        //   fill: $color-primary-light-1;
        //   width: 2.5rem;
        //   height: 2.5rem;
        // }

        border: 0.2rem solid $color-primary;
        &__icon {
          fill: $color-primary;
          width: 3rem;
          height: 3rem;
        }

        @extend .u-mb-5;
        @include rwd-df-down(xs) {
          margin: auto;
        }
      }

      &__text {
        &__title {
          font-family: $text-font;
          font-size: 1.5rem;
          font-weight: 500;
          color: $color-black;
          text-transform: capitalize;
          @extend .u-mb-1;
        }

        &__description {
          font-family: $text-font;
          font-size: 1.3rem;
          font-weight: normal;
          line-height: 1.85;
          color: $color-third-dark-1;
        }
      }
      &:hover &__icon-container {
        background-color: $color-fourth;
        .card-icon__icon-container__icon {
          fill: $color-primary-light-1;
        }
      }
    }
  }
}
