.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(#000, 0.75);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.8s;

  opacity: 0;
  visibility: hidden;
  transform: scaleY(0);
  transform-origin: top center;
  z-index: -1;

  &.show {
    opacity: 1;
    visibility: visible;
    z-index: 104;
    transform: scaleY(1);
  }

  &__content {
    position: relative;
    background-image: url('/content/img/newsletterbg.jpg');
    box-shadow: 0 0 7px 1px #282828;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 4.8rem;
    text-align: center;
    width: 35%;

    @include rwd-df-down(xl) {
      width: 50%;
    }

    @include rwd-df-down(md) {
      width: 60%;
    }

    @include rwd-df-down(sm) {
      width: 75%;
    }

    @include rwd-df-down(xs) {
      width: 90%;
      padding: 3.2rem;
    }
  }

  &__title {
    color: #282828;
    font-size: 3rem;
    font-weight: 600;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 3.2rem;
  }

  &__desc {
    color: #333;
    font-size: 1.6rem;
    text-align: center;
    margin-bottom: 2.4rem;
  }

  &__btn--close {
    position: absolute;
    top: 0;
    right: 0;
    border: 0.1rem solid #fff;
    display: block;
    background-color: #6f6f6f;
    transform: translate(50%, -50%);
    transition: all 0.3s;

    .icon {
      color: #fff;
      padding: 0.4rem;
      width: 3.2rem;
      height: 3.2rem;
      pointer-events: none;
    }

    &:hover {
      background-color: #333;
    }
  }

  .newsletters-form {
    input[type='email'] {
      display: block;
      width: 80%;
      background: #f7f7f7;
      border-radius: 0;
      font-size: 1.6rem;
      padding: 1.6rem;
      margin: 0 auto 2.4rem;
      width: 70%;
      @include rwd-df-down(xs) {
        width: 100%;
      }
    }
    button[type='submit'] {
      background-color: #ffae00;
      color: #fff;
      padding: 1rem 3.2rem;
      border: 0.2rem solid transparent;
      text-transform: capitalize;
      font-size: 1.6rem;
      font-weight: 600;
      text-transform: uppercase;
      transition: all 0.3s;

      &:hover {
        border-color: #ffae00;
        background-color: #fff;
        color: #ffae00;
      }
    }
  }
}
