/* define the typography clasess using for styling */

/*start define primary heading style */
.heading-primary {
  font-size: 2.2vw;
  margin-bottom: 3rem;
  font-family: $header-font;
  color: $color-black;
  font-weight: 600;
  line-height: 1.34;
  // text-align: center;
  text-transform: uppercase;
  @include rwd-df-down(sm) {
    font-size: 2rem;
  }
  @include rwd-only(xxs) {
    font-size: 1.8rem;
  }
}
/*end*/

/*start define padding section in all screens #DONT TOUCH HERE*/
.sec {
  font-family: inherit;
  padding: $section-padding-vertically-xxl 0;

  @include rwd-df-down(xl) {
    padding: $section-padding-vertically-xl 0;
  }

  @include rwd-df-down(lg) {
    padding: $section-padding-vertically-lg 0;
  }

  @include rwd-df-down(md) {
    padding: $section-padding-vertically-md 0;
  }

  @include rwd-df-down(sm) {
    padding: $section-padding-vertically-sm 0;
  }

  @include rwd-df-down(xs) {
    padding: $section-padding-vertically-xs 0;
  }

  @include rwd-only(xxs) {
    padding: $section-padding-vertically-xxs 0;
  }
}
/*end*/

/*start define section items in all screens #DONT TOUCH HERE*/
.sec--products {
  padding: 0;
  margin: $section-padding-vertically-xxl 0;
  @include rwd-df-down(xl) {
    margin: $section-padding-vertically-xl 0;
  }

  @include rwd-df-down(lg) {
    margin: $section-padding-vertically-lg 0;
  }

  @include rwd-df-down(md) {
    margin: $section-padding-vertically-md 0;
  }

  @include rwd-df-down(sm) {
    margin: $section-padding-vertically-sm 0;
  }

  @include rwd-df-down(xs) {
    margin: $section-padding-vertically-xs 0;
  }

  @include rwd-only(xxs) {
    margin: $section-padding-vertically-xxs 0;
  }
}
/*end*/

/*start define section items in all screens #DONT TOUCH HERE*/
.sec-items {
  border-radius: 0.5rem;
  box-shadow: 0 2px 5px 0 rgba($color-black, 0.5);
  // padding: 1rem;

  &__title {
    border-top-left-radius: 0.5rem;
    border-top-right-radius: 0.5rem;
    text-transform: capitalize;
    padding: 0.75rem;
    font-size: 2.5rem;
    font-family: $header-font;
    font-weight: 600;
  }

  &__header {
    padding: 0 1rem;
    @include rwd-df-down(xs) {
      padding: 0;
    }
    border-top-left-radius: 0.5rem;
    border-top-right-radius: 0.5rem;
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    &__title {
      text-transform: capitalize;
      padding: 0.75rem;
      font-size: 2.5rem;
      @include rwd-df-down(xs) {
        font-size: 2rem;
      }
      font-family: $header-font;
      font-weight: 600;
    }

    &__link {
      display: inline-block;
      text-transform: capitalize;
      padding: 0.75rem;
      font-size: 1.8rem;
      @include rwd-df-down(xs) {
        font-size: 1.4rem;
      }
      color: inherit;
      font-family: $header-font;
      font-weight: 600;
      display: flex;
      align-items: center;
      text-decoration: none;

      &__icon {
        fill: inherit;
        width: 3rem;
        height: 3rem;
      }
    }
  }
}
/*end*/
