.tagged {
  background-color: $color-third-light-1;

  &__header {
    &-list {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      @extend .u-my-4;
    }

    &__item {
      font-family: $header-font;
      font-size: $default-font-size;

      // @include rwd-df-down(xs) {
      //   font-size: 1.3rem;
      // }

      // @include rwd-only(xxs) {
      //   margin: $section-padding-vertically-xxs 0;
      // }

      font-weight: 600;
      padding: 1.5rem 2rem;
      @include rwd-df-down(xs) {
        padding: 1rem 1.5rem;
      }
      cursor: pointer;
      &:not(:last-child) {
        margin-#{$end}: 1rem;
      }
      border-bottom: 0.3rem solid $color-third-dark-3;
      transition: all 0.3s ease-in;
      &:hover,
      &.active {
        border-bottom-color: $color-primary;
        color: $color-primary;
      }
    }
  }

  &__body {
    &-list {
      // @extend .u-mt-4;
    }

    &__item {
    }
  }
}
