/* start Modern CSS Reset =>  https://dev.to/hankchizljaw/a-modern-css-reset-6p3 */

/*start CSS Box sizing rules*/
*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  box-sizing: border-box;
}
/*end*/

/*start Remove default padding rules*/
ul[class],
ol[class] {
  padding: 0;
}
/*end*/

/*start Remove default margin rules*/
body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}
/*end*/

/*start Set core root defaults*/
html {
  // Set root font => to set the default 1rem unit value
  font-size: 62.5%; // 1rem = 10px -->  10/16 *100% = 62.5%

  @include rwd-df-down(xs) {
    font-size: 56.25%;
  }

  // font-size: calc(14px + (14 - 9) * ((100vw - 1024px) / (1920 - 1024)));
  // font-size: calc(17px + (17 - 15) * ((100vw - 320px) / (768 - 320)));
}
/*end*/

/*start Set core body defaults*/
body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;

  font-family: $text-font;
  font-size: $default-font-size;
  font-weight: normal;
  font-style: normal;
  background-color: $color-third-light-1;
  color: $color-black;
}
/*end*/

/*start Remove list styles on ul, ol elements with a class attribute*/
ul[class],
ol[class] {
  list-style: none;
}
/*end*/

/*start set Anchore elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}
/*end*/

/*start Make images easier to work with*/
img {
  max-width: 100%;
  display: block;
}
/*end*/

/*start Natural flow and rhythm in articles by default*/
article > * + * {
  margin-top: 1em;
}
/*end*/

/*start Inherit fonts for inputs and buttons*/
input,
button,
textarea,
select {
  font: inherit;
}
/*end*/

